<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full  d-flex">
                            <h4 class="modal-title">{{ $t('StepDetails') }}</h4>
                            <button type="button" class="close" v-on:click="CloseStepDetails">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="listing">
                                <div class="table-responsive">
                                    <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th>{{ $t('Steps') }}</th>                                                
                                                <th>{{ $t('AccountStatus') }}</th>
                                                <th>{{ $t('EmailTemplates') }}</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="ListingData.length === 0">
                                                <td class="text-center text-danger no-record" colspan="8">
                                                    {{ $t('NoRecordfound') }}
                                                </td>
                                            </tr>
                                            <tr v-else v-for="(item, index) in ListingData" :key="index">
                                                <td>
                                                    {{ $t(item.Step_name) }}
                                                </td>                                               
                                                <td>
                                                    {{ item.status_name==null?'--':item.status_name }}
                                                </td>
                                                <td>
                                                    {{ item.template_name==null?'--':item.template_name }}
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props: {
        StepDetailsObj: {
            type: Object,
            require: true
        }
    },
    data() {
        return {
            isLoading: false,
            ListingData: []
        }
    },
    created: function () {
        var ths = this;
        ths.GetDetails();
    },
    methods: {
        GetDetails() {
            var vm = this;
            vm.isLoading = false;
            var url = `RuleId=${vm.StepDetailsObj.id}`;
            DataService.GetDetails(url).then(response => {
                vm.isLoading = false;
                if (response.data.result.DetailsData.length > 0) {
                    vm.ListingData = response.data.result.DetailsData;
                } else {
                    vm.ListingData = [];
                }
            })
        },
        CloseStepDetails() {
            var vm = this;
            vm.$emit('CloseStepDetails');
        },
    }
}
</script>