<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="true" ModuleName="Dunning"
            SubModuleCode="Dunning" IdentityColumn="id" :HeaderText="$t('Dunning')" :ListData="ListingData"
            :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons" :TotalRecords="TotalRecord"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchDunningList" :SortExp="SortExp"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"
            :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:ActionButtonClick="actionButtonClick">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_name'">
                    <a class="inactive-disabled">
                        <span class="text-center text-white d-block"
                            :style="{ backgroundColor: data.row.status_bg_color }">
                            {{ $t(data.row.status_name) }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    <span>
                        {{ $d(data.row.created_at) }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'StepCounts'">
                    <a class="task-dec" @click="ShowStepDeails(data.row)">
                        <span>
                            {{ $n(data.row.StepCounts) }}
                        </span>
                    </a>
                </template>
            </template>
        </tg-list>
        <AddDunningRule v-if="IsAddDunning" @CloseDunningRule="CloseDunningRule" :DunningObj="DunningObj">
        </AddDunningRule>
        <StepDetails v-if="IsStepDetails" :StepDetailsObj="StepDetailsObj" @CloseStepDetails="CloseStepDetails">
        </StepDetails>
    </section>
</template>
<script>
import AddDunningRule from './AddDunningRule.vue'
import StepDetails from './StepDetails.vue';
import DataService from '../../services/DataService';
export default {
    components: { AddDunningRule, StepDetails },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalRecord: 0,
            ListingData: [],
            IsAddDunning: false,
            IsStepDetails: false,
            StepDetailsObj: null,
            Headers: [
                {
                    "COLUMN_NAME": "title",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ExpenseTitle'),
                    "DISPLAY_ORDER": 1,

                },
                {
                    "COLUMN_NAME": "StepCounts",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'TotalStep',
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true
                    }

                },
                {
                    "COLUMN_NAME": "created_at",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'CREATED_ON',
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "created_by",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'created_by_name',
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "status_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'StatusName',
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                }
            ],
            listActions: [{
                title: 'Edit',
                icon: 'fa fa-edit text-success action-icon',
                additionalSpan: '',
                callbackfunction: 'EditDunning'
            },
            {
                title: 'Delete',
                href: 'javascript:void(0)',
                callbackfunction: 'DeleteDunning',
                icon: 'fa fa-trash-o action-icon text-danger',
                additionalSpan: ''
            }],
            Buttons: [{       
                title: this.$t('Add'),
                iconClass: 'fa fa-plus',
                callbackfunction: 'AddDunning',
                additionalClass: '',
                isdisabled: false,
                isvisible: true,
            }],
            DunningObj: null
        }
    },
    created: function () {
        var ths = this;
        ths.UserId = ths.GetUserInfo.ID;
        ths.CompanyId = ths.GetUserInfo.CompanyId;
        ths.Headers.forEach(element => {
            element.DISPLAY_NAME = ths.$t(element.DISPLAY_NAME)
        })
        ths.FetchDunningList();
    },
    methods: {
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    const fvalue = value.searchConditionJson.split('=')[1];
                    this.fav = fvalue;
                } else {
                    this.searchCondition = value.searchCondition;
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchDunningList();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchDunningList();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'EditDunning':
                    this.AddDunning(item);
                    break;
                case 'DeleteDunning':
                    this.commonDeleteData(item.id);
                    break;
            }
        },
        commonHeaderButtonClick: async function (event) {
            var vm = this;
            switch (event.callbackfunction) {
                case "AddDunning":
                    vm.AddDunning(null);
                    break;
            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchTaxCategoryList();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        AddDunning(val) {
            var ths = this;
            ths.DunningObj = val;
            ths.IsAddDunning = true;
        },
        CloseDunningRule() {
            var ths = this;
            ths.IsAddDunning = false;
            ths.FetchDunningList();
        },
        FetchDunningList() {
            var vm = this;
            vm.isLoading = true;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            DataService.FetchDunningList(url).then((response) => {
                vm.isLoading = false;
                if (response.data.result.DATA.length > 0) {
                    vm.ListingData = response.data.result.DATA;
                    vm.ListingData.forEach((item) => {
                        item.created_at = new Date(item.created_at)
                    })
                    vm.TotalRecord = response.data.result.DATA[0].TOTAL_RECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                } else {
                    vm.ListingData = [];
                    vm.TotalRecord = 0;
                }
            })
        },
        commonDeleteData: function (value) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            val = value
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Dunning")), function () {
                    var url = `ids=${val}`;
                    DataService.RemoveDunningMapping(url).then(function (response) {
                        vm.isLoading = false;
                        if (response.data.result.DATA.length > 0) {
                            if (response.data.result.DATA[0].VALUE == 'REMOVE_MAPPING') {
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Dunning')]), "Success", true, vm.$t('Alert'));
                                vm.FetchDunningList();
                            } else {
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Dunning')]), "failure", true, vm.$t('Alert'));
                            }
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Dunning')]), "failure", true, vm.$t('Alert'));
                        }
                    })
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        ShowStepDeails(val) {
            var ths = this;
            ths.StepDetailsObj = val;
            ths.IsStepDetails = true;
        },
        CloseStepDetails() {
            var ths = this;
            ths.IsStepDetails = false;
        }
    }
}
</script>
<style scoped>
.task-dec {
    color: #007bff !important;
    text-decoration: none;
    background-color: transparent;
}
</style>