                                                                                        <input name="Rate"
                                                                                            v-model="item.Rate"
                                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                                            @keypress="allowOnlyDigits($event)"
                                                                                            placeholder="Enter Rate" />
                                                                                        <span v-if="errors[0]"
                                                                                            :name="'Rate' + indexx"
                                                                                            class="invalid-feedback d-block">{{
                                                                                                $t('Ratefieldrequired')
                                                                                            }}
                                                                                        </span>
                                                                                    </Field>
                                                                                </div>
                                                                            </div>
                                                                            </div>
                                                                         </div>
                                                                        
                                                                         <div class="col-sm-1 col-md-1 col-lg-1 float-left">
                                                                            <div class="form-group mt-4 pt-1">                                                                               
                                                                                 <a @click="deleteCommonRow(index, indexx)">
                                                                                    <em class="fa fa-trash-o text-danger font-20"></em>
                                                                                 </a>
                                                                             </div>
                                                                         </div>
                                                                        
                                                                        </div>

                                                                    </div>
                                                                </div>
                                                                <div class="col-lg-12 p-0"
                                                                    v-if="item.Key == 'StartTerminationNoticePeriod'">
                                                                    <div class="col-lg-12 my-3 border p-3">
                                                                        <div class="row">
                                                                            <div
                                                                                class="col-sm-11 col-md-11 float-left col-lg-11">
                                                                                <div class="form-group mb-1">
                                                                                    <label>{{ indexx + 1 }}. {{
                                                                                        $t('StartTerminationNoticePeriod')
                                                                                        }}:</label>
                                                                                    <Field
                                                                                        :name="'NoticePeriod' + indexx"
                                                                                        v-slot="{ errors }"
                                                                                        :rules="{ 'required': true }">
                                                                                        <input name="NoticePeriod"
                                                                                            v-model="item.NoticePeriod"
                                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                                            @keypress="allowOnlyDigits($event)"
                                                                                            placeholder="Enter Notice Period" />
                                                                                        <span v-if="errors[0]"
                                                                                            :name="'NoticePeriod' + indexx"
                                                                                            class="invalid-feedback d-block">{{
                                                                                                $t('NoticePeriodfieldrequired')
                                                                                            }}
                                                                                        </span>
                                                                                    </Field>
                                                                                </div>
                                                                            </div>

                                                                            <div class="col-sm-1 col-md-1 col-lg-1 float-left">
                                                                            <div class="form-group mt-4 pt-1">                                                                               
                                                                                 <a @click="deleteCommonRow(index, indexx)">
                                                                                    <em class="fa fa-trash-o text-danger font-20"></em>
                                                                                 </a>
                                                                             </div>
                                                                         </div>

                                                                        </div>

                                                                    </div>
                                                                </div>
                                                                <div class="col-lg-12 p-0"
                                                                    v-if="item.Key == 'GenerateFinalBill'">
                                                                    <div class="col-lg-12 my-3 border p-3">
                                                                        <div class="row">
                                                                            <div
                                                                                class="col-sm-11 col-md-11 float-left col-lg-11">
                                                                                <div class="form-group mb-1">
                                                                                    <label>{{ indexx + 1 }}. {{
                                                                                        $t('GenerateFinalBill')
                                                                                        }}:</label>
                                                                                    <Field
                                                                                        :name="'IsGenerateFinalBill' + indexx"
                                                                                        v-slot="{ errors }"
                                                                                        :rules="{ 'required': true }">
                                                                                        <select
                                                                                            name="IsGenerateFinalBill"
                                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                                            v-model="item.IsGenerateFinalBill">
                                                                                            <option value="" selected>{{
                                                                                                $t('Select') }} {{
                                                                                                    $t('GenerateFinalBill')
                                                                                                }}</option>
                                                                                            <option
                                                                                                v-for="(item, index) in GenerateFinalBillData"
                                                                                                :key="index"
                                                                                                :value="item.value">{{
                                                                                                    item.name }}
                                                                                            </option>
                                                                                        </select>
                                                                                        <span v-if="errors[0]"
                                                                                            :name="'IsGenerateFinalBill' + indexx"
                                                                                            class="invalid-feedback d-block">{{
                                                                                                $t('GenerateFinalBillfieldrequired')
                                                                                            }}
                                                                                        </span>
                                                                                    </Field>
                                                                                </div>
                                                                            </div>
                                                                       <div class="col-sm-1 col-md-1 col-lg-1 float-left">
                                                                            <div class="form-group mt-4 pt-1">                                                                               
                                                                                 <a @click="deleteCommonRow(index, indexx)">
                                                                                    <em class="fa fa-trash-o text-danger font-20"></em>
                                                                                 </a>
                                                                             </div>
                                                                         </div>
                                                                       
                                                                            </div>
                                                                        
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div v-if="item.RuleData.TriggerData.length == 0"
                                                                class="text-center text-danger border p-2 norecord">
                                                                {{ $t("NorecordfoundText") }}
                                                            </div>
                                                        </Form>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="row pt-2">
                                    <div class="col-6">
                                        <div class="py-2">
                                            <span class="mandatory">{{ $t('MandatoryString') }}.</span>
                                        </div>
                                    </div>
                                    <div class="text-end text-right col-6">
                                        <a href="javascript:;" class="btn btn-success" @click="SaveDunning"><em
                                                class="fa fa-save pr-2"></em>{{ $t('Save') }}</a>
                                        <a href="javascript:;" class="btn btn-danger" style="margin-left: 5px;"
                                            @click="CloseDunningRule"><em class="fa fa-close pr-2"></em>{{ $t('Cancel')
                                            }}</a>
                                    </div>
                                </div>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <AddTrigger v-if="IsTrigger" :IsTriggerIndex="IsTriggerIndex" @CloseAddTrigger="CloseAddTrigger"
            @AddTrigger="AddTrigger"></AddTrigger>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import AddTrigger from './AddTrigger.vue';
export default {
    components: { AddTrigger },
    props: {
        DunningObj: {
            type: Object
        }
    },
    data() {
        return {
            isLoading: false,
            DunningTitle: '',
            activeIndex: 0,
            ListingData: [
                {
                    StepName:'Step1',
                    DatabaseCondition:'',
                    RuleData:{
                        ConditionData:[
                            {
                                Code: '',
                                ConditionId: '',
                                OperatorId: '',
                                Values: '',
                                DatabaseCondition:'',
                                OperatorData:[],
                                BillingStatus:[]
                            }
                        ],
                        TriggerData:[]                        
                    },
                    isFinal: false
                },
                { 
                    StepName: 'FinalStep', 
                    DatabaseCondition:'',
                    RuleData:{
                        ConditionData:[
                            {
                                Code: '',
                                ConditionId: '',
                                OperatorId: '',
                                Values: '',
                                DatabaseCondition:'',
                                OperatorData:[],
                                BillingStatus:[]
                            }
                        ],
                        TriggerData: []
                    },
                    isFinal: true
                }
            ],
            stepCounter: 2,
            maxSteps: 4,
            AccountStatus: [],
            EmailTemplates: [],
            Id: 0,
            BillingStatus: [],
            ConditionsData:[],
            OperatorData:[],
            InvoiceOperatorData:[],
            GenerateFinalBillData:[],
            IsTrigger:false,
            IsTriggerIndex:null,
            RateTypeData: [{ name: 'Percentage', value: 'P' }, { name: 'Flat', value: 'F' }],
            InvoiceStatus:[],
            PaymentStatus:[]
            
        }
    },
    created: function () {
        var ths = this;
        ths.GetDunningConditions();
        ths.GetBillStatusInvoice();
        ths.GetBillStatusPaymnet();
        ths.GetOperator();
        ths.GetPaymentInvoiceOperator();
        ths.GetEmailTemplate();
        ths.GetDunningGenerateFinalBill();
        ths.GetSubscriptionStatus();
        if (ths.DunningObj != null) {
            setTimeout(function () {
                    ths.BindDetails();
            }, 1000);            
        }
    },
    methods: {
        GetOperator(){
            var ths=this;
            var url = `id=&moduleName=&type=DUNNING_CONDITIONS_OPERATORS&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            ths.OperatorData = response.data.DATA;
                        }
                    }
                }
            })
        },
        GetPaymentInvoiceOperator(){
            var ths=this;
            var url = `id=&moduleName=&type=DUNNING_CONDITIONS_SELECT_OPERATORS&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            ths.InvoiceOperatorData = response.data.DATA;
                        }
                    }
                }
            })
        },
        GetBillStatusInvoice() {
            var vm = this;
            var url = `type=Invoice`
            DataService.GetBillStatus(url).then((response) => {
                if (response.data.result.data.length > 0) {
                    vm.InvoiceStatus = response.data.result.data;
                }
            })
        },  
        GetBillStatusPaymnet() {
            var vm = this;
            var url=`type=ClientBilling`
            DataService.GetBillStatus(url).then((response) => {
                if (response.data.result.data.length > 0) {
                    vm.PaymentStatus = response.data.result.data;
                }
            })
        },         
        GetDunningConditions() {
            var vm = this;
            var url = `id=&moduleName=&type=DUNNING_CONDITIONS&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            vm.ConditionsData = response.data.DATA;
                        }
                    }
                }
            })
        },  
        onConditionChange(currentCondition, stepItem) {            
            const duplicate = stepItem.RuleData.ConditionData.some(cond =>
                cond !== currentCondition && cond.ConditionId === currentCondition.ConditionId
            );
            if (duplicate) {
                this.ShowAlert(this.$t('conditionalreadyexists'), "warning", true, this.$t("Alert"));
                currentCondition.ConditionId = ''; 
                return;
            }
            this.SetConditions(currentCondition); 
            this.computeDatabaseCondition(stepItem);
        },             
        SetConditions(itm) {
            var vm=this;            
            const condition = this.ConditionsData.find(item => item.value === itm.ConditionId);
            if (condition) {
                itm.Code = condition.first_value;
            }
            if(condition.value=='payment_status'){
                itm.BillingStatus=vm.PaymentStatus;               
            }
            if(condition.value=='status_id'){
                itm.BillingStatus=vm.InvoiceStatus;               
            }
            if(condition.value=='payment_status' || condition.value=='status_id'){
                itm.OperatorData=vm.InvoiceOperatorData;                
            }
            else{
                itm.OperatorData=vm.OperatorData;                
            }
        },
        GetEmailTemplate() {
            var vm = this;
            var url = `id=&moduleName=&type=DUNNING_EMAIL_TEMPLATE&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            vm.EmailTemplates = response.data.DATA;
                        }
                    }
                }
            })
        },
        GetDunningGenerateFinalBill() {
            var vm = this;
            var url = `id=&moduleName=&type=DUNNING_GENERATE_FINAL_BILL&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            vm.GenerateFinalBillData = response.data.DATA;
                        }
                    }
                }
            })
        },
        GetSubscriptionStatus() {
            var vm = this;
            var url = `id=&moduleName=&type=DUNNING_ACCOUNT_STATUS&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            vm.AccountStatus = response.data.DATA;
                        }
                    }
                }
            })
        },
        BindDetails() {
            var ths = this;
            var url = `Id=${ths.DunningObj.id}`;
            ths.isLoading = true;
            DataService.GetDunningDetails(url).then(response => {                
                ths.ListingData=[];
                if (response.data.result.DATA.length > 0) {
                    debugger
                    ths.Id = response.data.result.DATA[0].id;
                    ths.DunningTitle = response.data.result.DATA[0].title;
                    if( response.data.result.DATA[0].ListingData.length>0){
                        response.data.result.DATA[0].ListingData.forEach(step=>{
                            var ConditionData=[];
                            if(step.RuleData[0].ConditionData.length>0){
                                step.RuleData[0].ConditionData.forEach(ele=>{
                                    var Obj={
                                        Code: ele.ConditionId=='payment_status' || ele.ConditionId=='status_id' ?'select':'input',
                                        ConditionId: ele.ConditionId,
                                        OperatorId: ele.OperatorId,
                                        Values: ele.Values,
                                        DatabaseCondition:`${ele.ConditionId} ${ele.OperatorId} ${ele.Values}`,
                                        BillingStatus:[],
                                        OperatorData:[]
                                    }                                    
                                    if(ele.ConditionId =='payment_status'){   
                                       Obj.BillingStatus = ths.PaymentStatus;                
                                    }
                                    if(ele.ConditionId =='status_id'){
                                       Obj.BillingStatus = ths.InvoiceStatus;
                                    }
                                    if(ele.ConditionId=='payment_status' || ele.ConditionId=='status_id'){ 
                                        Obj.OperatorData = ths.InvoiceOperatorData;
                                    }
                                    else{
                                        Obj.OperatorData = ths.OperatorData;
                                    }
                                    ConditionData.push(Obj)
                                })
                            }
                            if (step.RuleData[0].TriggerData.length > 0) {
                                var TriggerData = []
                                step.RuleData[0].TriggerData.forEach(ele => {
                                    if (ele.generate_final_bill_configuration == 'GenerateFinalBill') {
                                        var Obj = {
                                            Key: ele.generate_final_bill_configuration,
                                            IsGenerateFinalBill: ele.generate_final_bill_payments
                                        }
                                        TriggerData.push(Obj);
                                    }
                                    if (ele.late_fees_applicable_configuration == 'ApplyLateFee') {
                                        var Obj = {
                                            Key: ele.late_fees_applicable_configuration,
                                            Fee_Type: ele.late_fee_type,
                                            Rate: ele.late_fee_amount
                                        }
                                        TriggerData.push(Obj);
                                    }
                                    if (ele.late_payment_on_partial_payments_configuration == 'PartialPaymentLateFee') {
                                        var Obj = {
                                            Key: ele.late_payment_on_partial_payments_configuration,
                                            Fee_Type: ele.partial_late_fee_type,
                                            Rate: ele.partial_late_fee_amount
                                        }
                                        TriggerData.push(Obj);
                                    }
                                    if (ele.send_email_notification_configuration == 'SendEmailNotification') {
                                        var Obj = {
                                            Key: ele.send_email_notification_configuration,
                                            Template_Id: ele.send_email_notification_id
                                        }
                                        TriggerData.push(Obj);
                                    }
                                    if (ele.termination_configuration == 'StartTerminationNoticePeriod') {
                                        var Obj = {
                                            Key: ele.termination_configuration,
                                            NoticePeriod: ele.termination_notice_period
                                        }
                                        TriggerData.push(Obj);
                                    }
                                    if (ele.update_account_status_configuration == 'UpdateAccountStatus') {
                                        var Obj = {
                                            Key: ele.update_account_status_configuration,
                                            Status_Id: ele.update_account_status_id
                                        }
                                        TriggerData.push(Obj);
                                    }
                                    if (ele.tiered_late_payment_charges == 'TieredLateFee') {
                                        var TieredLateFeeData = [];
                                        if (ele.TieredLateFeeData.length > 0) {
                                            var TieredLateFeeData = [];
                                            ele.TieredLateFeeData.forEach(item => {
                                                var Obj = {
                                                    FromDay: item.FromDay,
                                                    ToDay: item.ToDay,
                                                    Rate: item.Rate
                                                }
                                                TieredLateFeeData.push(Obj);
                                            })
                                            var Obj = {
                                                Key: ele.tiered_late_payment_charges,
                                                TieredLateFeeType: ele.tiered_late_fee_type,
                                                TieredLateFeeData: ele.TieredLateFeeData
                                            }
                                            TriggerData.push(Obj);
                                        }
                                    }
                                })
                            }
                            var conditionStrings='';
                            conditionStrings = ConditionData.map(cond => {                                
                                if (cond.ConditionId && cond.OperatorId && cond.Values !== '') {
                                    const value = cond.Values;
                                    const quotedValue = isNaN(value) || cond.Code === 'select'
                                        ? value
                                        : value;
                                    const conditionSQL = `${cond.ConditionId} ${cond.OperatorId} ${quotedValue}`;
                                    cond.DatabaseCondition = conditionSQL;
                                    return conditionSQL;
                                } else {
                                    cond.DatabaseCondition = '';
                                    return null;
                                }
                            }).filter(Boolean);
                            var DatabaseCondition = conditionStrings.length > 0
                                ? `(${conditionStrings.join(' AND ')})`
                                : '';
                            
                            var obj = {
                                DatabaseCondition:DatabaseCondition,
                                StepName: step.StepName,
                                RuleData: {
                                    ConditionData: ConditionData,
                                    TriggerData: TriggerData
                                }
                            }
                            ths.ListingData.push(obj);
                            ths.isLoading = false;
                        })
                    }
                }
            })

        },
        CloseDunningRule() {
            var vm = this;
            vm.$emit('CloseDunningRule');
        },
        allowOnlyDigits(event) {
            const charCode = event.charCode;
            if (charCode < 48 || charCode > 57) {
                event.preventDefault();
            }
        },
        OnSubmit() {
            var ths = this;
            if (ths.ListingData.length >= ths.maxSteps) {
                ths.ShowAlert(ths.$t('FinalStepCannotAddMore'), "warning", true, ths.$t("Alert"));
                return;
            }
            const newStep = {
                StepName: `Step${ths.stepCounter}`,
                isFinal:false,
                DatabaseCondition:'',
                RuleData:{
                    ConditionData:[ 
                        {
                            Code: '',
                            ConditionId: '',
                            OperatorId: '',
                            Values: '',
                            DatabaseCondition:''
                        }
                    ],
                    TriggerData:[]
                    
                }
            };
            ths.ListingData.splice(ths.ListingData.length - 1, 0, newStep);
            let stepNumber = 2;
            ths.ListingData.forEach((step, idx) => {
                if (!step.isFinal && idx !== 0) {
                    step.StepName = `Step${stepNumber}`;
                    stepNumber++;
                }
            });
            ths.activeIndex = insertIndex;
            ths.stepCounter++; 
            
            ths.ListingData.forEach(step => {
                if (!step.isFinal && step.RuleData?.ConditionData?.length > 0) {                    
                    ths.computeDatabaseCondition(step);
                }
            });
        },      
        computeDatabaseCondition(step) {
            if (!step?.RuleData?.ConditionData) return;
            const conditionStrings = step.RuleData.ConditionData.map(cond => {
                if (cond.ConditionId && cond.OperatorId && cond.Values !== '') {
                    const value = cond.Values;
                    const quotedValue = isNaN(value) || cond.Code === 'select'
                        ? value
                        : value;
                    const conditionSQL = `${cond.ConditionId} ${cond.OperatorId} ${quotedValue}`;
                    cond.DatabaseCondition = conditionSQL;
                    return conditionSQL;
                } else {
                    cond.DatabaseCondition = '';
                    return null;
                }
            }).filter(Boolean);
            step.DatabaseCondition = conditionStrings.length > 0
                ? `(${conditionStrings.join(' AND ')})`
                : '';
        },
        deleteRow(index) {
            var ths = this;
            if (index === 0 || ths.ListingData[index].isFinal) {
                return false;
            }
            ths.ListingData.splice(index, 1);
            let stepNumber = 2;
            ths.ListingData.forEach((step, idx) => {
                if (!step.isFinal && idx !== 0) {
                    step.StepName = `Step${stepNumber}`;
                    stepNumber++;
                }
            });
            ths.ListingData.forEach(step => {
                if (!step.isFinal && step.RuleData?.ConditionData?.length > 0) {
                    ths.computeDatabaseCondition(step);
                }
            });

        },
        SaveDunning() {
            var vm = this;
            vm.isLoading = true;
            vm.$refs.AddDunningStep.validate().then(async valid => {
                if (valid) {
                    var Obj = {
                        Id: vm.Id,
                        Title: vm.DunningTitle,
                        DunningData: vm.ListingData
                    }
                    var JsonObj = JSON.stringify(Obj);
                    DataService.SaveDunning(JsonObj).then(function (response) {
                        vm.isLoading = false;
                        if (response.data.Result.DATA.length > 0) {
                            if (response.data.Result.DATA[0].VALUE == 'UPDATE') {
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Dunning')]), "success", true, vm.$t("Alert"));
                                vm.CloseDunningRule();
                            }
                            else if (response.data.Result.DATA[0].VALUE == 'EXISTS') {
                                vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Dunning')]), "warning", true, vm.$t("Alert"));
                            }
                            else {
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Dunning')]), "success", true, vm.$t("Alert"));
                                vm.CloseDunningRule();
                            }
                        }
                    })
                } else {
                    vm.isLoading = false;
                }
            })
        },
        AddTriggeredActions(index) {
            var ths = this;
            ths.IsTriggerIndex = index;
            ths.IsTrigger = true;
        },
        CloseAddTrigger() {
            var ths = this;
            ths.IsTrigger = false;
        },
        AddConditions(stepItem) {
            var vm = this;
            vm.$refs.AddDunningCondition[0].validate().then(valid => {
                if (valid) {
                    const newCondition = {
                        ConditionId: '',
                        OperatorId: '',
                        Values: '',
                        Code: '',
                        OperatorData: [],
                        BillingStatus: []
                    };
                    stepItem.RuleData.ConditionData.push(newCondition);
                    vm.computeDatabaseCondition(stepItem);
                }
            })
        },
        deleteCondition(item, index) {
            item.RuleData.ConditionData.splice(index, 1);
        },
        AddTrigger(payload) {
            var ths = this;
            const { stepIndex, trigger } = payload;
            const step = ths.ListingData[stepIndex];
            if (!step) return;
            const exists = step.RuleData.TriggerData.some(t => t.Key === trigger.Obj.Key);
            if (!exists) {
                step.RuleData.TriggerData.push(trigger.Obj);
            }
            else {
                this.ShowAlert(this.$t('AlreadyExists', [this.$t('Trigger')]), "warning", true, this.$t("Alert"));
                return;
            }
            ths.CloseAddTrigger();
        },
        deleteCommonRow(stepIndex, triggerIndex) {
            const step = this.ListingData[stepIndex];
            if (step && step.RuleData && step.RuleData.TriggerData) {
                step.RuleData.TriggerData.splice(triggerIndex, 1);                
            }
        },
        addTieredRow(itm) {
            var ths = this;
            ths.$refs.AddDunningTieredLateFee[0].validate().then(valid => {
                if(valid){
                    const lastRow = itm.TieredLateFeeData[itm.TieredLateFeeData.length - 1];
                    const newFrom = parseFloat(lastRow.ToDay) + 0.01;                    
                     itm.TieredLateFeeData.push({
                        FromDay: newFrom.toFixed(2),
                        ToDay: '',
                        Rate: ''
                    });
                }
            })

        },
        removeTieredRow(parentItem, index) {
            if (parentItem.TieredLateFeeData.length > 1) {
                parentItem.TieredLateFeeData.splice(index, 1);
            }
        },
        toggleCard(index) {
            this.activeIndex = this.activeIndex === index ? null : index;
        },
    }
}
</script>
<style>
.settop {
    right: 7px;
    top: 7px;
}
</style>