<template>
    <section>
        <div  class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full  d-flex">
                            <h4 class="modal-title">{{ $t('AddTrigger') }}</h4>
                            <button type="button" class="close" v-on:click="CloseAddTrigger">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AddTaxCategoryForm" :mandatorytext='MandatoryString'
                                 v-on:OnSubmit="OnSubmit">
                            </dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
            </div>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props:{
        IsTriggerIndex:{
            type:Number
        }
    },
    data(){
        return{
            isLoading:false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [               
                {
                    layoutType: "single",
                    Data: [ 
                        {
                            astype: "SelectField",                                                
                            label: this.$t('TriggerType'),
                            name: "TriggerType",
                            value: "", 
                            visibility: true,   
                            config: {
                                options: []
                            },                           
                            placeholder: this.$t('TriggerType'),
                            validationRules: { "required": true, max: 100 },
                        }                                                                                     
                    ]
                }                               
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",                    
                    text: "<i class='fa fa-save pr-2'></i>Save"                   
                },            
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseAddTrigger,
                },
            ],
            TriggerData:[],
            TieredLateFeeData: [{
                FromDay: 1.00,
                ToDay: '',
                Rate: ''
            }]
        }
    },
    created: function () {
        var ths = this;
        ths.GetAddTrigger();       
    },
    methods:{
        CloseAddTrigger(){
            var vm = this;
            vm.$emit('CloseAddTrigger');
        },
        GetAddTrigger() {
            var vm = this;
            var url = `id=&moduleName=&type=DUNNING_ADD_TRIGGER&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            vm.TriggerData=response.data.DATA;
                            vm.TriggerData.forEach(element => {
                                let Obj={
                                    name:element.name,
                                    value:element.value
                                }
                                vm.FormSchema[0].Data[0].config.options.push(Obj);
                            });
                            vm.$refs.AddTaxCategoryForm.UpdateKeyValue();                           
                        }
                    }
                }
            })
        },
        OnSubmit(){
            var vm = this;
            var val=vm.FormSchema[0].Data[0].value;
            if (val == 'ApplyLateFee' || val == 'PartialPaymentLateFee') {
                var Obj = {
                    Key: val,
                    Fee_Type: '',
                    Rate: ''
                }
            }
            if (val == 'SendEmailNotification') {
                var Obj = {
                    Key: val,
                    Template_Id: ''
                }
            }
            if (val == 'UpdateAccountStatus') {
                var Obj = {
                    Key: val,
                    Status_Id: ''
                }
            }
            if (val == 'TieredLateFee') {
                var Obj = {
                    Key: val,
                    TieredLateFeeType: '',
                    TieredLateFeeData: vm.TieredLateFeeData
                }
            }
            if (val == 'StartTerminationNoticePeriod') {
                var Obj = {
                    Key: val,
                    NoticePeriod: ''
                }
            }
            if (val == 'GenerateFinalBill') {
                var Obj = {
                    Key: val,
                    IsGenerateFinalBill: ''
                }
            }
            vm.$emit('AddTrigger',{stepIndex:vm.IsTriggerIndex,trigger:{Obj}});
        }
    }
}
</script>